/* home.css */
.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to bottom right, #1a1a1a, #000000);
  color: white;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: opacity 0.5s ease-in-out;
}

.home-container.fade-in {
  opacity: 1;
}

.home-container.fade-out {
  opacity: 0;
}

.home-logo {
  height: 160px;
  animation: pulse 2s infinite;
}

.home-title {
  font-size: 3rem;
  font-weight: 800;
  margin-top: 20px;
}

.home-cta {
  font-size: 1.5rem;
  color: #bbbbbb;
  margin-top: 10px;
  animation: bounce 1.5s infinite;
}

/* Overlay for effect */
.home-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.2);
  pointer-events: none;
}

/* Animations */
@keyframes pulse {
  0% { transform: scale(1); opacity: 0.8; }
  50% { transform: scale(1.05); opacity: 1; }
  100% { transform: scale(1); opacity: 0.8; }
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}
