/* OrderType Page */
.ordertype-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to bottom right, #1a1a1a, #000000);
  color: white;
  text-align: center;
  transition: opacity 0.5s ease-in-out;
}

.ordertype-container.fade-in {
  opacity: 1;
}

/* Title */
.ordertype-title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 20px;
}

/* Buttons Container */
.ordertype-buttons {
  display: flex;
  gap: 20px;
}

/* Square Buttons */
.ordertype-btn {
  background: white;
  color: black;
  font-size: 1.2rem;
  padding: 20px;
  width: 150px;
  height: 150px;
  border: 3px solid white;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.2s, background 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

/* Button Icons */
.ordertype-icon {
  width: 50px;
  height: 50px;
}

/* Button Hover Effect */
.ordertype-btn:hover {
  background: black;
  color: white;
}

/* Press Animation */
.ordertype-btn.pressed {
  transform: scale(0.9);
  background: #ffcc00;
  color: black;
  transition: background 0.3s;
}
